import React from 'react';
import { Row, Col } from '../Grid';
import './style.scss';

function Facts(props) {
    return (
        <div className="remove-left-margin fact">
            {props.icon === "fas fa-umbrella-beach" ? (
                <div onClick={(event) => props.openGlobe(event)} className="pointer">
                    <Row>
                        <Col size="12">
                            <div className="center-content">
                                <i className={props.icon} />
                            </div>
                        </Col>
                        <Col size="12">
                            <div className="center-content">
                                <p className="fact-desc">
                                    {props.fact}
                                </p>
                            </div>
                        </Col>
                    </Row>
                </div>
            ) : (
                    <Row>
                        <Col size="12">
                            <div className="center-content">
                                <i className={props.icon} />
                            </div>
                        </Col>
                        <Col size="12">
                            <div className="center-content">
                                <p className="fact-desc">
                                    {props.fact}
                                </p>
                            </div>
                        </Col>
                    </Row>
                )}
        </div>
    )
}

export default Facts;