import React, { useState } from 'react';
import { Row, Col } from '../Grid';
import Globe from '../../assets/video/trip-globe.mp4'
import Loading from '../Loading';
import './style.scss';

function GlobeModal(props) {
    const [loaded, setLoaded] = useState(false);

    return (
        <div className="center-content project-modal">
            <div className="modal-side" onClick={(event) => props.closeGlobe(event)} />
            <div className="modal-content-section">
                <Row>
                    <Col size="12">
                        <video className="" loop autoPlay playsInline muted aria-label="video-demo"
                            style={{
                                objectFit: 'cover',
                                width: '100%',
                                height: '100vh',
                            }}
                            onLoad={() => setLoaded(true)}
                            src={Globe}>
                        </video>
                        {
                            !loaded &&
                            <div className="center-content">
                                <Loading />
                            </div>
                        }
                    </Col>
                </Row>
            </div>
            <div className="modal-side" onClick={(event) => props.closeGlobe(event)} />
        </div>
    )
}

export default GlobeModal;